import React, {useState} from 'react'
import {Link, useNavigate} from 'react-router-dom'
import Badge from "react-bootstrap/Badge"
import Cart from "../screens/Carts"
import Modal from "../Modal.js"
import { useCart } from '../components/contextreducer';
export default function Navbar() {
  let data  = useCart();
  const [cartView, setCartView] = useState(false)
  const navigate = useNavigate()
  const handleLogout = () =>{
    localStorage.removeItem("authToken")
    navigate('/login')
  }
  return (
    <div>
        <nav className="navbar navbar-expand-lg navbar-dark bg-success">
  <div className="container-fluid">
    <Link className="navbar-brand fs-2 fst-italic" to="/">FoodHub</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav me-auto mb-2">
        <li className="nav-item">
          <Link className="nav-link active fs-5" aria-current="page" to="/">Home</Link>
        </li>
         {/* implementing how to know whether the user is already looged in */}
         {localStorage.getItem("authToken") ? 
          <li className="nav-item">
          <Link className="nav-link active fs-5" aria-current="page" to="/myOrder">My orders</Link>
        </li> 
      : ""}
      </ul>
      {/* implementing that part where when you are not logged in so these two have to be shown */}
      {(!localStorage.getItem("authToken")) ?
      <div className='d-flex'>
        <Link className="btn bg-white text-success mx-1" to="/login">Login</Link>
        <Link className="btn bg-white text-success mx-1" to="/createuser">SignUp</Link>
      </div>
      : 
      <div>
      <div className='btn bg-white text-success mx-2' onClick={()=>{setCartView(true)}}>
        Mycart {" "}
        <Badge pill bg = "danger">{data.length}</Badge>
        </div>
        {cartView ? <Modal onClose = {()=>{setCartView(false)}}><Cart/></Modal> : null}
      <div className='btn bg-white text-danger mx-2' onClick = {handleLogout}>Logout</div>
      </div>
       }
    </div>
  </div>
</nav>
    </div>
  )
}
