import React,{useState} from 'react'
import {Link} from 'react-router-dom'

export default function Signup() {
    const [credentials, setCredentials] = useState({ name :"", email : "", password : "", geolocation : ""})
    const handleSubmit = async(e) => {
        e.preventDefault();
        const response  = await fetch("https://foodappserver.onrender.com/api/createuser", {
            method : "POST",
            mode : "cors",
            headers : {
                'content-type' : "application/json"
            },
            body : JSON.stringify({name : credentials.name, email : credentials.email, password : credentials.password, location : credentials.geolocation})
        });
        const json = await response.json()
        console.log(json)
        if(!json.success)
        {
            alert("Enter Valid Credentials")
        }
    }
    const onChange = (e) => {
        setCredentials({...credentials, [e.target.name] : e.target.value})
    }
    return (
    <>
    <div className='container'>
    <form onSubmit = {handleSubmit}>
    <div className="mb-3">
    <label htmlFor="name" className="form-label">Name</label>
    <input type="text" className="form-control" name = "name" value = {credentials.name} onChange = {onChange}/>
  </div>
  <div className="mb-3">
    <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
    <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" name =  "email" value = {credentials.email} onChange ={onChange}/>
    <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
  </div>
  <div className="mb-3">
    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
    <input type="password" className="form-control" name = "password" value = {credentials.password}id="exampleInputPassword1" onChange = {onChange}/>
  </div>
  <div class="mb-3">
    <label for="inputAddress" className="form-label">Address</label>
    <input type="text" className="form-control" name = "geolocation" value = { credentials.geolocation} id="inputAddress" placeholder="1234 Main St" onChange = {onChange}/>
  </div>
  <button type="submit" className="btn btn-success">Submit</button>
  <Link to = "/login" className = "m-3 btn btn-danger">already a user</Link>
</form>
</div>
 </>
  )
}
