import React,{useState,useEffect}from 'react'
import Navbar from "../components/Navbar"
import Footer from "../components/Footer"
import Card from "../components/Card"

// import Carousal from "../components/Carousal"
export default function() {
  const [search,setSearch] = useState("")
  const [foodCat, setFoodcat] = useState([]);
  const [foodItem, setFooditem] = useState([])
  // getting data from frontend 
  const loaddata = async() =>{
    let response = await fetch("https://foodappserver.onrender.com/api/foodData", {
      method :"post",
      mode : "cors",
      headers : {
        "content-type" : "application/json"
      }
    });
    response = await response.json();
    setFooditem(response[0])
    setFoodcat(response[1])
  }
  
  useEffect( ()=> {
    loaddata()
  },[])
  
  
  return (
    <div>
        <div> <Navbar/> </div>
        <div> 
        <div id="carouselExampleFade" className="carousel slide carousel-fade" data-bs-ride="carousel" style = {{objectFit : "contain !important"}}>
        <div className="carousel-inner" id = "carousel">
        <div className="carousel-caption" style = {{zIndex : "10"}}>
            <div className="d-flex justify-content-center">
           <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" value = {search} onChange = {(e) => {setSearch(e.target.value)}}/>
          {/* <button className="btn btn-outline-success text-white bg-success" type="submit">Search</button> */}
          </div>
        </div>
        <div className="carousel-item active">
        <img src="https://source.unsplash.com/random/900x700/?burger" className="d-block w-100" style = {{filter : "brightness(30%)"}} alt="..."/>
        </div>
        <div className="carousel-item">
        <img src="https://source.unsplash.com/random/900x700/?noodles" className="d-block w-100" style = {{ filter : "brightness(30%)"}} alt="..."/>
        </div>
        <div className="carousel-item">
        <img src="https://source.unsplash.com/random/900x700/?pastry" className="d-block w-100" style = {{filter : "brightness(30%)"}} alt="..."/>
        </div>
        </div>
        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Previous</span>
        </button>
        <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Next</span>
        </button>
        </div>
        </div>
        <div className='container'> 
         {
            foodCat !== []
            ? foodCat.map( (data)=> {
              return (
                <div className='row mb-3'>
              <div key = {data._id} className='fs-3 m-3'>
                {data.CategoryName}
                </div>
                <hr/>
                 { foodItem !== [] ? foodItem.filter( (item) => (item.CategoryName === data.CategoryName)  && (item.name.toLowerCase().includes(search.toLowerCase())))
                    .map( (filteredItem) => {
                      return (
                        <div key ={filteredItem._id} className='col-12 col-md-6 col-lg-3'>
                             <Card foodItem = {filteredItem}
                              options = {filteredItem.options[0]}
                             >
                             </Card>
                        </div>
                      )
                    })
                 : <div> item not available </div>}
                </div>
              )
            })
            : ""
         } 
        </div>
        <div> <Footer/> </div>
    </div>
  )
}
